
.carousel-list-item {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
  position: relative;
}
.blueTextBox{
  color: #fdfdfd;
  background: #6bbef8;
  top: 110px;
  -webkit-transition: 1.2s  0.4s;
  -o-transition: 1.2s  0.4s;
  transition: 1.2s  0.4s;
  width: 28%;
}
.whiteTextBox{
  color: #70828f;
  background-color:#ffffff;
  top: 180px;
  -webkit-transition: 1.5s  0.4s;
  -o-transition: 1.5s  0.4s;
  transition: 1.5s  0.4s;
  width: 26%;
}
.orangeTextBox {
  color: #fdfdfd;
  background: #f6a258;
  top: 260px;
  -webkit-transition: 2s  0.4s;
  -o-transition: 2s  0.4s;
  transition: 2s  0.4s;
  width: 24%;
}

.TextBox {
  position: absolute;
  left: -300px;
  height: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  font-family: "Roboto";
  padding: 10px 15px;
  z-index: 0;
  min-width: 250px;
  display: flex;
  text-align: left;
}
.slick-active .TextBox {
  -webkit-transform: translateX(100%);
      -ms-transform: translateX(100%);
          transform: translateX(100%);
  z-index: 7;
}

.slick-dots li button::before {
    font-size: 15px;
}

.slick-prev {
    left: 30px;
    z-index: 4;
}

.slick-next {
    right: 30px;
    z-index: 4;
}

.slick-dots {
    bottom: 30px;
    z-index: 4;
}
.slick-slide img {
    z-index: 5;
}



